import React, {Component} from 'react';
import {connect} from 'react-redux';
import {push} from 'react-router-redux';
import PropTypes from 'prop-types';
import {actionCreator} from '../../../actions';
import * as types from '../../../constants/actiontypes';
import RaisedButton from 'material-ui/RaisedButton';
import {Table, TableBody, TableHeader, TableHeaderColumn, TableRow, TableRowColumn} from 'material-ui/Table';
import QueueAnim from 'rc-queue-anim';
import {FetchHelper} from '../../../helpers/fetchhelper';
import DialogSection from '../components/Dialog/index';
import moment from 'moment'


const styles = {
  buttonColumn: {
    textAlign: 'center',
    verticalAlign: 'middle'
  },
  buttonOnRow: {
    margin: '0 10px',
  },
}

class DetailMaker extends Component {
  tableHeight = '500';

  constructor() {
    super();
    moment.locale('ja');
  }

  state = {
    open: false,
  };

  componentDidMount() {
    const {match, location} = this.props;
    this.props.init(match.params.makerid);
  }


  register = (e) => {
    this.props.clickRegister(this.props.makerId);
  }

  edit = (houseid) => {
    this.props.clickEdit(houseid);
  }

  display = (houseId, houseTitle) => {
    this.props.clickDisplay(true, houseId, houseTitle);
  }

  render() {
   
    return (
      <section className="container-fluid with-maxwidth chapter">
        <QueueAnim type="bottom" className="ui-animate">

          <div key="1">
            <article className="article">
              <h2 className="article-title">メーカー詳細</h2>
              <div className="box box-default">
                <div className="box-body padding-xl">

                  <div className="form-group row">
                    <label className="col-md-2 control-label">メーカーID</label>
                    <div className="col-md-10">
                      <label className="control-label">{this.props.makerId}</label>
                    </div>
                  </div>
                  <div className="form-group row">
                    <label className="col-md-2 control-label">メーカー名</label>
                    <div className="col-md-10">
                      <label className="control-label">{this.props.makerName}</label>
                    </div>
                  </div>
                  <div className="form-group row">
                    <label className="col-md-2 control-label">事業部名</label>
                    <div className="col-md-10">
                      <label className="control-label">{this.props.division}</label>
                    </div>
                  </div>
                  <div className="form-group row">
                    <label className="col-md-2 control-label">郵便番号</label>
                    <div className="col-md-10">
                      <label className="control-label">{this.props.zipcode}</label>
                    </div>
                  </div>
                  <div className="form-group row">
                    <label className="col-md-2 control-label">住所</label>
                    <div className="col-md-10">
                      <label className="control-label">{this.props.address}</label>
                    </div>
                  </div>
                  <div className="form-group row">
                    <label className="col-md-2 control-label">電話番号</label>
                    <div className="col-md-10">
                      <label className="control-label">{this.props.tel}</label>
                    </div>
                  </div>
                  <div className="form-group row">
                    <label className="col-md-2 control-label">FAX</label>
                    <div className="col-md-10">
                      <label className="control-label">{this.props.fax}</label>
                    </div>
                  </div>
                  <div className="form-group row">
                    <label className="col-md-2 control-label">URL</label>
                    <div className="col-md-10">
                      <label className="control-label">{this.props.url}</label>
                    </div>
                  </div>
                  <div className="form-group row">
                    <label className="col-md-2 control-label">Email</label>
                    <div className="col-md-10">
                      <label className="control-label">{this.props.email}</label>
                    </div>
                  </div>
                  <div className="form-group row">
                    <label className="col-md-2 control-label">登録日</label>
                    <div className="col-md-10">
                      <label className="control-label">{this.props.createdAt}</label>
                    </div>
                  </div>
                </div>
              </div>
            </article>

            <div className="form-group row">
              <div className="col-md-10"></div>
              <div className="col-md-2">
                <RaisedButton label="建物登録" className="btn-w-md" onClick={(event) => this.register(event)} />
              </div>
            </div>

            <div className="box box-default table-box table-flip-scroll mdl-shadow--2dp" >
              <Table height={this.tableHeight}>
                <TableHeader displaySelectAll={false}>
                  <TableRow>
                    <TableHeaderColumn>建物ID</TableHeaderColumn>
                    <TableHeaderColumn>建物コード</TableHeaderColumn>
                    <TableHeaderColumn>登録日</TableHeaderColumn>
                    <TableHeaderColumn></TableHeaderColumn>
                  </TableRow>
                </TableHeader>
                
                <TableBody displayRowCheckbox={false}>
                  {this.props.houseList.map((item, index) => {
                    return (
                      <TableRow key={index} selectable={false}>
                        <TableRowColumn>{item.house_id}</TableRowColumn>
                        <TableRowColumn>{item.house_code}</TableRowColumn>
                        <TableRowColumn>{moment(item.created_at).format('ll')}</TableRowColumn>
                        <TableRowColumn style={styles.buttonColumn}>
                          <RaisedButton label="編集" className="btn-w-xs" style={styles.buttonOnRow} onClick={(event) => this.edit(item.house_id)} />
                          <RaisedButton label="CG表示" className="btn-w-xs" style={styles.buttonOnRow} onClick={(event) => this.display(item.house_id, item.house_title)} />
                        </TableRowColumn>
                      </TableRow>
                    )
                  })}
                </TableBody>
                
              </Table>
            </div>
         
          </div>
        </QueueAnim>

        <DialogSection isOpen = {this.props.dialogOpen} houseId={this.props.houseId} houseTitle={this.props.houseTitle} />
      </section>
    );
  }
}
DetailMaker.propTypes = {
  clickRegister: PropTypes.func,
  clickEdit: PropTypes.func,
  clickDisplay: PropTypes.func,
};


const mapStateToProps = state => ({
  makerId: state.detailMakerSettings.makerId,
  makerName: state.detailMakerSettings.makerName,
  address: state.detailMakerSettings.address,
  url: state.detailMakerSettings.url,
  address: state.detailMakerSettings.address,
  url: state.detailMakerSettings.url,
  division: state.detailMakerSettings.division,
  zipcode: state.detailMakerSettings.zipcode,
  tel: state.detailMakerSettings.tel,
  fax: state.detailMakerSettings.fax,
  url: state.detailMakerSettings.url,
  email: state.detailMakerSettings.email,
  createdAt: state.detailMakerSettings.createdAt,
  houseList: state.detailMakerSettings.list || [],

  dialogOpen: state.detailMakerSettings.dialogOpen || false,
  houseTitle: state.detailMakerSettings.houseTitle || '',
  houseId: state.detailMakerSettings.houseId || 0,
});

const mapDispatchToProps = dispatch => ({
  init: (makerid) => {
    const helpder = new FetchHelper();

    helpder.execute('/auth/check', 'post', {}, () => {
      let params = {
        makerid: makerid
      }
      helpder.execute('/find/maker', 'POST', params, (result) => {
        dispatch(actionCreator(types.DETAIL_MAKER, result));        
      });
    });
  },
  clickRegister: (makerId) => {
    dispatch(push('/app/createhouse/' + makerId));
  },
  clickEdit: (houseid) => {
    dispatch(push(`/app/edithouse/${houseid}/detailmaker`));
  },
  clickDisplay: (isOpen, houseId, houseTitle) => {
    dispatch(actionCreator(types.DISPLAY_HOUSE, [isOpen, houseId, houseTitle]));
  },
  
});

module.exports = connect(mapStateToProps, mapDispatchToProps)(DetailMaker);


