import React, {Component} from 'react';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import {actionCreator} from '../../../../actions';
import * as types from '../../../../constants/actiontypes';
import RaisedButton from 'material-ui/RaisedButton';
import FlatButton from 'material-ui/FlatButton';
import Dialog from 'material-ui/Dialog';
import {FetchHelper} from '../../../../helpers/fetchhelper';
import * as consts from '../../../../constants/consts';

class ModelViewer extends Component {

  componentDidMount() {
    const { scene } = this.refs;
  }

  width = 600;
  height = 400;

  customBodyStyle = {
    margin:'0 auto',
    width: this.width,
    height: this.height,
    padding: '0 !important'
  };

  customContentStyle = {
    overflow: 'hidden',
    
  };

  constructor(props, context) {
    super(props, context);

    //this.cameraPosition = new THREE.Vector3(0, 0, 5);
  }

  close = () => {
    this.props.clickClose();
  };

  render() {
    const actions = [
      <FlatButton
        label="閉じる"
        primary
        keyboardFocused
        onClick={this.close} />,
    ];

    const url = `/extra/modeview.html?houseid=${this.props.houseId}&baseurl=${consts.baseUrl}`;
   
    return (
        <Dialog 
          title={this.props.houseTitle}
          actions={actions}
          modal={false} 
          bodyStyle={this.customBodyStyle}
          contentStyle={this.customContentStyle}
          open={this.props.isOpen}
          onRequestClose={this.handleClose}
        >
          <div>
            {/* <React3 mainCamera="camera" width={this.width} height={this.height} >
              <scene ref="scene">
                <perspectiveCamera
                  name="camera"
                  fov={75}
                  aspect={this.width / this.height}
                  near={0.1}
                  far={1000}
                  position={this.cameraPosition}
                />
                <mesh>
                  <boxGeometry width={1} height={1} depth={1} />
                  <meshBasicMaterial color={0x00ff00} />
                </mesh>
              </scene>
            </React3> */}
            <iframe src={url} width={this.width} height={this.height} frameBorder={"0"}></iframe>
          </div>
      </Dialog>
    );
  }
}
ModelViewer.propTypes = {
  clickClose: PropTypes.func
};

const mapStateToProps = state => ({
  isOpen: state.detailMakerSettings.dialogOpen || false,
});

const mapDispatchToProps = dispatch => ({
  clickClose: () => {
    dispatch(actionCreator(types.DISPLAY_HOUSE, false));
  },
});

module.exports = connect(mapStateToProps, mapDispatchToProps)(ModelViewer);

const DialogSection = () => (
  <section>
    <ModelViewer />
  </section>
);

export default DialogSection;



